body {
    font-family: "Lufga", sans-serif;
    background: $ivory;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 500;
    margin-bottom: 16px;
    line-height: 1.25;

    strong {
        font-weight: 800;
    }
}

h1 {
    font-size: 40px;

    &.page-title {
        font-weight: 900;
        text-transform: uppercase;
    }
}

h2 {
    font-size: 32px;
    @media (max-width: 767px) {
      font-size:24px;
    }
}

h3 {
    font-size: 28px;
    @media (max-width: 767px) {
      font-size: 20px;
    }
}

h4 {
    font-size: 24px;
    @media (max-width: 767px) {
      font-size: 16px;
    }
}

h5 {
    font-size: 20px;
    @media (max-width: 767px) {
      font-size: 12px;
    }
}

h6 {
    font-size: 18px;
    @media (max-width: 767px) {
      font-size: 10px;
    }
}

ol,
ul {
    padding-left: 20px;
}

a {
    text-decoration: unset;
    cursor: pointer;

    &:hover {
        color: $yellow;
    }
}

button,
.button {
    border-radius: 8px;
    background: #f3f4f6;
    padding: 0 18px;
    min-height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #010101;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    @media (min-width: 1250px) {
      &:hover {
          color: $black;
          background: $yellow;
      }
    }
}

img {
    max-width: 100%;
}
main {
  padding-top: 110px;
  padding-bottom: 60px;
  @media (max-width: 767px) {
    padding-top: 71px;
    position: relative;
    width: 100%;
    overflow: hidden;
  }
}
.container {
    margin: auto;
    max-width: 1142px;
    width: 100%;
    padding: 0 16px;
}

#primary {
    min-height: 70vh;
}
#page-title h1 {
  margin: 0;
  @media (max-width: 767px) {
      font-size: 24px;
  }
}
.section {
    overflow: hidden;

    .container {
        position: relative;
        padding: 0;
        max-width: 1110px;
    }

    &.dark {
        color: $white;
    }

    &-head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 0 20px;
        @media (max-width: 767px) {
            margin: 0 0 8px;
        }
        h2 {
            margin: 0;
        }

        a {
            color: $grey;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 5px;
            font-size: 14px;

            @media (min-width: 768px) {
                font-size: 16px;
            }
        }
    }

    &-title {
        font-size: 20px;

        @media (min-width: 768px) {
            font-size: 28px;
        }
    }

    &.text {
        &.disclaimer {
            margin: 20px 0 60px;
            color: #010101;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 150%;
            &:last-child {
              margin: 0;
            }
            @media (max-width: 767px) {
                font-size: 14px;
            }
        }
        h2,
        h3,
        h4,
        h5,
        h6 {
            margin: 24px 0 8px;
            &:first-child {
              margin-top: 0;
            }
        }
        ul {
          margin: 8px 0 24px;
          padding: 0;
          list-style: none;
          li {
            position: relative;
            padding-left: 12px;
            &::after {
              content: '';
              position: absolute;
              left: 0;
              top: 9px;
              width: 4px;
              height: 4px;
              background: #010101;
              border-radius: 50%;
            }
          }
        }
    }
    &.comics {
        .slick {
            &-slide {
                margin: 0 8px;
                &r {
                    margin: 0 -8px;
                }
                & > div {
                    &:not(:last-of-type) {
                        margin-bottom: 18px;
                    }
                }
            }
            &-arrow {
                width: 42px;
                height: 42px;
                padding: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 100%;
                border: unset;
                background: $button-white;
                position: absolute;
                top: calc(50% - 21px);
                cursor: pointer;
                &:hover,
                &:focus {
                    background: $yellow;
                    outline: unset;
                }
                &.slick {
                    &-prev {
                        left: -75px;
                    }
                    &-next {
                        right: -75px;
                    }
                }
            }
        }
    }
}

.section:not(.no-padding),
aside {
    padding: 0 16px;
}

.comics {
    &-grid {
        display: grid;
        gap: 6px;
        grid-template-columns: repeat(6, 1fr);

        &.double-line {
            grid-template-columns: repeat(6, 2fr);
            @media (max-width: 1023px) {
                grid-template-columns: repeat(3, 1fr);
                padding-bottom: 0;
            }
            @media (max-width: 767px) {
              display: grid;
              grid-template-rows: repeat(2, 251px);
              grid-auto-flow: column;
              overflow-x: auto;
              &::-webkit-scrollbar {
                height: 4px;
                background-color: #e0e0e0;
                border-radius: 4px;
              }

              &::-webkit-scrollbar-thumb {
                border-radius: 10px;
                background: #9d9aa2;
              }

              &::-webkit-scrollbar-track {
                background-color: #f3f4f6;
                border-radius: 4px;
              }
            }
            .comics {
              min-width: 128px;
            }
        }

        @media (max-width: 1023px) {
            grid-template-columns: repeat(3, 1fr);
            padding-bottom: 15px;
        }

        @media (min-width: 1024px) {
            gap: 16px 12px;
        }

        .comics {
            @media all and (min-width: 768px) {
                min-width: initial;
            }
        }
    }
}

.modal {
    display: none;
    position: fixed;
    top: 0;
    height: 0;
    z-index: 999;
    width: 100vw;
    height: 100vh;
    padding: 0 16px;
    &.open {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__overlay {
        position: absolute;
        z-index: 1;
        background: rgba(0, 0, 0, .25);
        width: 100%;
        height: 100%;
    }
    &__content {
        max-width: 520px;
        width: 100%;
        position: relative;
        z-index: 2;
        background: #fff;
        border-radius: 20px;
        padding: 36px 32px;
        @media all and (max-width: 767px) {
            padding: 36px 16px;
        }
    }
    &__title {
      color: #010101;
      display: block;
      text-align: center;
      font-size: 20px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%;
      margin-bottom: 24px;
    }
    &__row {
      position: relative;
      margin-bottom: 14px;
    }
    &__label {
      position: absolute;
      left: 14px;
      top: 0;
      color: #4c4c4c;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      pointer-events: none;
      transform: translateY(16px);
      transition: all .3s ease-in-out;
      &.active {
        font-size: 12px;
        transform: translateY(4px);
      }
    }
    &__show-pass {
      top: 17px;
      position: absolute;
      right: 14px;
      width: 17px;
      height: 16px;
      display: flex;
      align-items: center;
      justify-content: center;
      &.active {
        .eye-2 {
          display: block;
        }
        .eye-1 {
          display: none;
        }
      }
    }
    .error {
      color: #e90000;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      margin-top: 4px;
    }
    &__forgot-row {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 26px;
    }
    &__forgot-pass {
      color: #4c4c4c;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      text-decoration: underline;
      @media (min-width: 1250px) {
        &:hover {
          color: #4c4c4c;
          text-decoration: none;
        }
      }
    }
    &__btn {
      display: flex;
      height: 44px;
      padding: 0;
      justify-content: center;
      align-items: center;
      border: none;
      width: 100%;
      cursor: pointer;
      border-radius: 100px;
      background: #010101;
      color: #fff;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 140%;
      transition: all .25s ease-in-out;
      @media (min-width: 1250px) {
        &:hover {
          color: #feec49;
          background: #010101;
          text-decoration: none;
        }
      }
    }
    &__bottom {
      padding-top: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #4c4c4c;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
    }
    &__text {
      color: #010101;
      text-align: center;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      margin-bottom: 20px;
      max-width: 290px;
      margin: 0 auto 20px;
      text-align: center;
    }
    &__cont-btn {
      display: flex;
      height: 44px;
      padding: 0;
      justify-content: center;
      align-items: center;
      border: none;
      width: 100%;
      cursor: pointer;
      border-radius: 100px;
      background: #010101;
      color: #fff;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 140%;
      transition: all .25s ease-in-out;
      @media (min-width: 1250px) {
        &:hover {
          color: #feec49;
          background: #010101;
          text-decoration: none;
        }
      }
    }
    &__close {
      position: absolute;
      right: 20px;
      top: 20px;
      cursor: pointer;
      svg {
        display: block;
        fill: #010101;
      }
      @media (min-width: 1250px) {
        &:hover {
          svg {
            fill: #f00;
          }
        }
      }
    }
}
.sign-up-btn,
.sign-in-btn {
  margin-left: 5px;
  color: #4c4c4c;
  text-decoration: underline;
  @media (min-width: 1250px) {
    &:hover {
      color: #4c4c4c;
      text-decoration: none;
    }
  }
}
.eye-1 {
  display: block;
}
.eye-2 {
  display: none;
}
div,
article {
    &.comics {
        border-radius: 12px;
        position: relative;

        .comic {
            &-thumbnail {
                min-height: 148px;
                position: relative;
                overflow: hidden;
                border-radius: 12px;

                .labels,
                .episodes,
                .views {
                    position: absolute;
                    z-index: 1;
                }

                .views,
                .labels {
                    padding: 4px;
                }

                .views,
                .episodes {
                    bottom: 0;
                }

                .episodes,
                .views>span {
                    padding: 3px 6px;
                    display: inline-flex;
                    background: $views;
                }

                .label {
                    color: $white;
                    padding: 4px;
                    font-size: 10px;
                    line-height: 1;
                    font-weight: 500;
                    border-radius: 30px;

                    &s {
                        top: 0;
                        left: 0;
                        display: flex;
                        gap: 5px;
                        align-items: center;
                    }

                    &.adult {
                        background: linear-gradient(to right, $soft-red 3%, $red 95%);
                        padding: 5px 7.5px;
                        font-size: 12px;
                    }

                    &.end {
                        background: linear-gradient(to right, $soft-purple 3%, $medium-purple 95%);
                    }
                }


                .views {
                    left: 0;

                    &>span {
                        color: $white;
                        border-radius: 10px;
                        padding: 4px;
                        align-items: center;
                        gap: 2px;
                        font-size: 12px;
                    }
                }

                .episodes {
                    right: 0;
                    color: $yellow;
                    border-radius: 10px 0;
                    font-size: 12px;
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: center;
                    border-radius: 12px;
                    transition: all .25s ease-in-out;
                    z-index: 0;
                }

                &:hover {
                    img {
                        transform: scale(1.1);
                    }
                }
            }

            &-title {
                color: $black;
                font-weight: 500;
                margin: 0;
                font-size: 14px;
                height: 34px;
                overflow: hidden;
                text-overflow: ellipsis;
                line-clamp: 2;
                -webkit-line-clamp: 2;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                @media (max-width: 767px) {
                  font-size: 12px;
                  font-weight: 500;
                  line-height: 130%;
                  height: 32px;
                }
            }

            &-content {
                padding: 6px 0 0;
            }

            &-genre {
                font-size: 12px;
                font-weight: 500;
                display: block;
                line-height: 150%;
                padding-bottom: 2px;
                @media (max-width: 767px) {
                  font-size: 10px;
                  line-height: 140%;
                }
            }
        }
    }

    &.banner {
        min-height: 300px;
        border-radius: 20px;
        font-size: 14px;
        line-height: 20px;

        .content {
            padding: 22px 16px 13px;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            display: flex;
            flex-direction: column;
            gap: 16px;

            &.center {
                justify-content: center;
            }

            &.bottom {
                justify-content: flex-end;
            }

            &.between {
                justify-content: space-between;
            }
        }

        img {
            border-radius: 20px;
            width: 100%;
        }
    }
}
.see-all {
  transition: all .3s ease-in-out;
  path {
    transition: all .3s ease-in-out;
  }
  @media (min-width: 1250px) {
    &:hover {
        color: $yellow;
        svg path {
          stroke: $yellow !important;
        }
    }
  }
}
.content-page {
  color: #0d0c0c;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  h1 {
    color: #010101;
    font-size: 44px;
    font-style: normal;
    font-weight: 700;
    line-height: 105%;
    margin: 0 0 16px;
  }
  h2 {
    color: #010101;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin: 0 0 10px;
  }
  p {
    margin: 10px 0;
  }
  ul {
    margin: 10px 0;
    li + li {
      margin-top: 10px;
    }
  }
}
