.footer {
    background: $black;
    color: $white;
    font-size: 16px;
    line-height: 1.25;
    &-content {
        border-bottom: 1px solid #202020;
        padding-bottom: 56px;
        @media (min-width: 768px) {
          display: flex;
          justify-content: space-between;
        }
        @media (max-width: 767px) {
          padding-bottom: 44px;
        }
        .menu {
            list-style: none;
            padding: 0;
            margin: 0;
            li {
              & + li {
                margin-top: 12px;
              }
            }
            a {
                color: $white;
                font-weight: 400;
                cursor: pointer;
                &:hover {
                    color: $yellow;
                }
            }
        }
        &__navigation {
          display: flex;
          @media (min-width: 768px) {
            justify-content: flex-end;
            align-items: flex-start;
            gap: 100px;
            max-width: 644px;
            width: 100%;
          }
          @media (max-width: 1023px) {
            gap: 60px;
          }
          @media (max-width: 767px) {
            gap: 0;
            padding-top: 20px;
            flex-wrap: wrap;
            .menu-container {
              width: 100%;
              order: 1;
            }
          }
        }
    }
    hr {
        margin: 24px 0;
    }
    &-mobile {
        padding: 40px 16px;
        .menu {
            flex-direction: column;
            gap: 10px;
        }
        hr {
            border-color:#181818;
        }
        .footer-copyright {
            font-size: 12px;
            color: #999999;
        }
    }
    &-desktop {
        padding: 40px 0;
        .menu {
            gap: 24px;
        }
        .social {
          @media (max-width: 767px) {
            margin-bottom: 36px;
          }
          a {
            background: rgba(255, 255, 255, 0.10);
            svg {
              fill: #fff;
            }
            @media (min-width: 1250px) {
              &:hover {
                svg {
                  fill: #febc49;
                }
              }
            }
          }
        }
    }
}
.footer-bottom {
  padding-top: 56px;
  @media (max-width: 767px) {
    padding-top: 44px;
  }
  &__company {
    color: rgba(255,255,255,0.8);
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 24px;
    @media (min-width: 768px) {
      display: flex;
    }
    p {
      margin: 0;
    }
  }
  &__company-item {
    @media (min-width: 768px) {
      max-width: 343px;
      width: 100%;
    }
    & + .footer-bottom__company-item {
      margin-left: 54px;
      @media (max-width: 767px) {
        margin: 14px 0 0;
      }
    }
  }
}
.footer-copyright {
  color: rgba(255,255,255,0.8);
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}
@media (max-width: 767px) {
  .menu-container + .menu-container {
    margin-top: 36px;
  }
}

.menu-container + .menu-container a {
  color: rgba(255,255,255,0.8);
}
