// @use 'parts/slider';
// @use 'parts/genres';
.home-logo {
  margin: 0;
  font-size: 10px;
  span {
    position: absolute;
    left: -9999px;
    display: block;
  }
}
.logo {
  margin-top: -4px;
}
.home {
  .comics-grid {
    margin-bottom: 0;
  }
  main {
    padding-top: 66px;
  }
    .section {
        margin-bottom: 54px;
        @media (max-width: 767px) {
          margin-bottom: 36px;
        }
        &.section-slider {
          margin-bottom: 4px;
          @media (max-width: 767px) {
            margin-bottom: 20px;
            padding-left: 16px;
          }
        }
        h2 {
            &.section-title {
                line-height: 150%;
                display: flex;
                align-items: center;
                svg {
                  margin-right: 10px;
                }
                sub {
                  font-size: 16px;
                  font-weight: 400;
                  line-height: 140%;
                  margin-left: 6px;
                  @media (max-width: 767px) {
                    font-size: 14px;
                    bottom: -1px;
                  }
                }
            }
        }
        &#discount-40 {
            h2 {
                text-transform: uppercase;
            }
            .button {
                width: 100%;
                max-width: 216px;
                align-self: center;
                justify-content: center;
                padding: 10px 16px;
            }
        }
        &#exclusive-titles {
            h5 {
                margin: 0;
            }
        }
    }
}
