.single-comics {
  main {
    padding-top: 66px;
    @media (max-width: 767px) {
      padding-top: 52px;
    }
  }
}
.label {
    align-items: center;
    display: flex;
    gap: 4px;
    &__item {
        border-radius: 8px 0;
        background: #ffc68c;
        padding: 2px 4px;
        text-align: center;
        color: $white;
        &--public {
            align-items: center;
            background: $black;
            color: #fff;
            display: flex;
            justify-content: center;

            svg {
                margin-right: 4px;
            }
        }
    }
}

.comic {
    &-general {
        &-inner {
            position: relative;

            @media (min-width: 992px) {
                align-items: center;
                display: flex;
                gap: 30px
            }

            @media (min-width: 1200px) {
                gap: 40px
            }
        }

        &-img {
            margin: 0 -16px 10px;
            position: relative;

            @media (min-width: 992px) {
                margin: 0;
                order: 1;
                width: 50%;
            }

            @media (min-width: 1200px) {
                flex: 1 0 640px;
            }

            img {
                height: auto;
                width: 100%;

                @media (min-width: 992px) {
                    border: 2px solid $black;
                    border-radius: 24px;
                    filter: drop-shadow(4px 4px 0 $black);
                }
            }
        }

        &__labels {
            align-items: center;
            flex-wrap: wrap;
            margin-bottom: 4px;

            .label__item {
                font-size: 12px;
                font-style: normal;
                font-weight: 500;
                line-height: 16px;

                &--uncensored {
                    font-weight: 700;
                }
            }
        }

        &__desc {
            font-size: 14px;
            margin-bottom: 0;

            @media (min-width: 992px) {
                max-height: 146px;
                overflow-x: hidden;
                overflow-y: scroll;
                -ms-overflow-style: none;
                margin-right: 6px;
                padding-right: 6px;
                scrollbar-width: none
            }

            &::-webkit-scrollbar {
                border-radius: 999px;
                width: 4px;

                &-track {
                    box-shadow: inset 0 0 2px transparent
                }

                &-thumb {
                    background-clip: padding-box;
                    background-color: $black;
                    border-radius: 10px
                }
            }
        }

        .tags-wrapper {
            margin-bottom: 8px;

            .comic-tag {
                color: var(--main-grey, $devil-black);
                display: inline-block;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: 16px;
                margin-bottom: 8px;
                margin-right: 8px;
                text-decoration-line: underline;

                &.hidden {
                    display: none
                }
            }

            .show-more {
                background: transparent;
                border: none;
                color: var(--main-black, $black);
                cursor: pointer;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: 16px;
                text-decoration-line: underline;

                &:before {
                    color: var(--main-grey, $devil-black);
                    content: " . . . ";
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: 16px;
                    margin-right: 8px;
                    text-decoration-line: none
                }
            }

            &.show-all {
                .show-more {
                    &:before {
                        content: none
                    }
                }
            }
        }

        &__story-art {
            font-size: 14px;
            line-height: 16px;
            color: $devil-black;

            span {
                color: $black;
                font-weight: 700;
            }

            a {
                color: $devil-black;
                font-weight: 500;
                text-decoration: underline;
                position: relative;
                margin-right: 8px;

                &:after {
                    content: "/";
                    position: absolute;
                    right: -9px;
                }

                &:last-of-type:after {
                    content: none;
                }
            }
        }
    }

    &__btn {

        &-favorite,
        &-rate {
            background: transparent;
            border: 0;
            font-size: 14px;
            outline: none;
            padding: 0;

            svg {
                height: 20px;
                width: auto;

                @media (min-width: 768px) {
                    height: 24px;
                }
            }

            .on {
                display: none;
            }

            >span {
                display: flex;
                gap: 8px;
                align-items: center;
                color: $black;
            }

            &.in-favorite,
            &.rated {
                .off {
                    display: none;
                }

                .on {
                    display: inline-flex;
                }
            }
        }

        &_uncensored {
            img {
                margin-right: 9px;
            }
        }

        @media (min-width: 768px) {
            padding: 14px 24px;
        }

        &s {
            display: flex;
            flex-direction: column;
            gap: 8px;
            align-items: flex-start;
            justify-content: flex-start;

            @media (max-width: 992px) {
                .btn {
                    width: 100%;
                }
            }
        }
    }
}

.noten {
    .comic-general {
        &__story-art {
            font-size: 12px;

            @media (min-width: 768px) {
                font-size: 14px;
            }
        }
    }
}
.section:not(.no-padding).comic-general {
  @media (max-width: 767px) {
    padding: 0;
  }
}
.comic-general {
  @media (max-width: 767px) {
    padding: 0;
  }
  &__top {
    margin: 20px 0 28px;
    position: relative;
    @media (max-width: 767px) {
        margin: 0 0 28px;
    }
  }
  &__image {
    position: relative;
    border-radius: 16px;
    overflow: hidden;
    @media (max-width: 767px) {
      border-radius: 0;
    }
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(0deg, #1B1223 0%, rgba(0, 0, 0, 0.00) 100%);
      height: 157px;
      @media (max-width: 768px) {
        height: 120px;
      }
    }
  }
  &__back {
    position: absolute;
    top: 20px;
    left: 32px;
    border-radius: 8px;
    background: rgba(0, 0, 0, 0.20);
    backdrop-filter: blur(14px);
    color: #fff;
    font-family: 'Lufga';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    z-index: 1;
    padding: 6px 16px;
    transition: all .3s ease-in-out;
    display: flex;
    align-items: center;
    @media (max-width: 767px) {
      text-indent: -9999px;
      top: 10px;
      left: 16px;
      padding: 5px 13px;
    }
    @media (min-width: 1250px) {
      &:hover {
        svg {
          stroke: #feec49;
        }
      }
    }
    svg {
      margin-right: 8px;
      stroke: #fff;
      transition: all .3s ease-in-out;
      @media (max-width: 767px) {
        margin: 0;
      }
    }
  }
  &__info {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 28px 32px;
    z-index: 1;
    @media (max-width: 767px) {
      padding: 16px 10px 16px 14px;
    }
    .comic-general__labels {
      justify-content: space-between;
      align-items: center;
    }
    .views {
      border-radius: 100px;
      background: rgba(0, 0, 0, 0.30);
      backdrop-filter: blur(14px);
      padding: 0 6px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      svg {
        margin-right: 2px;
      }
    }
  }
  &__title {
    color: #fff;
    font-size: 44px;
    font-style: normal;
    font-weight: 700;
    line-height: 105%;
    margin: 0 0 8px;
    @media (max-width: 767px) {
      font-size: 20px;
      font-weight: 500;
    }
  }
  &__add-info {
    display: flex;
    align-items: center;
    margin: 0;
  }
  &__rating-stars {
    color: #fff;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    border-radius: 100px;
    background: rgba(0, 0, 0, 0.30);
    backdrop-filter: blur(14px);
    padding: 0 6px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 4px;
    svg {
      margin-right: 2px;
    }
  }
  &__rating-stars-rating {
    span {
      color: #f3f4f6;
      font-size: 10px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
    }
  }
  &__old {
    border-radius: 100px;
    background: linear-gradient(81deg, #F44043 3.28%, #C70028 94.73%);
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 34px;
    color: #fff;
    text-align: center;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-left: 4px;
    padding-left: 1px;
  }
  &__info-items {
    display: flex;
    .label__item {
      line-height: 20px;
      & + .label__item {
        margin-left: 4px;
      }
    }
  }
}
.container.comic-list {
  margin-bottom: 60px;
  @media (min-width: 1024px) {
    display: flex;
    justify-content: space-between;
    overflow: visible;
    align-items: flex-start;
  }
}
.comic-list-inner {
  max-width: 727px;
  width: 100%;
  @media (max-width: 1023px) {
    margin-bottom: 36px;
  }
}
.comic-list-sidebar {
  padding: 0;
  width: 100%;
  @media (min-width: 1024px) {
    position: sticky;
    top: 0;
    max-width: 343px;
    margin-left: 15px;
    .comic-archive-item {
      display: flex;
      align-items: center;
      & + .comic-archive-item {
        margin-top: 16px;
      }
    }
    .comic-content {
      margin-left: 12px;
    }
  }
  &__title {
    color: #010101;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin: 0 0 14px;
  }
  .comic-archive-item > a {
    max-width: 110px;
    flex-shrink: 0;
  }
  div.comics .comic-title,
  article.comics .comic-title {
    font-weight: 400;
    margin-bottom: 15px;
    @media (max-width: 767px) {
      font-size: 12px;
      margin-bottom: 5px;
    }
  }
  div.comics .comic-descr,
  article.comics .comic-descr {
    overflow: hidden;
    color: #010101;
    display: -webkit-box;
   -webkit-line-clamp: 2;
   -webkit-box-orient: vertical;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    @media (max-width: 767px) {
      font-size: 10px;
    }
  }
}
.description-wrapper {
  margin-bottom: 16px;
  p {
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}
.start-reading {
  border-radius: 100px;
  background: #feec49;
  padding: 0 20px;
  color: #010101;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 140%;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  margin-bottom: 28px;
  transition: all .3s ease-in-out;
  position: relative;
  span,
  svg {
    position: relative;
    z-index: 1;
  }
  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
    transition: all .3s ease-in-out;
    opacity: 0;
    border-radius: 100px;
  }
  @media (min-width: 1250px) {
    &:hover {
      color: #010101;
      &::after {
        opacity: 1;
      }
    }
  }
}
.comics-list-head__quantity {
  color: #010101;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  margin: 0;
}
.comic-list-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  &__sort-by {
    margin: 0;
    display: flex;
    align-items: center;
    color: #010101;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    cursor: pointer;
    &.active {
      svg {
        transform: rotate(180deg);
      }
    }
    svg {
      margin-right: 6px;
      flex-shrink: 0;
    }
  }
}
.comic-list {
  &__item {
    display: flex;
    justify-content:space-between;
    align-items: center;
    border-radius: 12px;
    background: #fff;
    box-shadow: 0px 1px 30px 0px rgba(61, 61, 61, 0.08);
    padding: 8px 10px 8px 8px;
    text-decoration: none;
    border-bottom: 1px solid transparent;
    transition: all .3s ease-in-out;
    @media (min-width: 1250px) {
      &:hover {
        border-bottom-color: #feec49;
        .comic-list__img img {
          transform: scale(1.06);
        }
      }
    }
    & + .comic-list__item {
      margin-top: 6px;
    }
    &--free {
      .comic-list__btn {
        color: #9D9AA2;
      }
    }
    &--read {
      .comic-list__btn {
        color: #31d676;
      }
    }
  }
  &__content {
    display: flex;
    align-items: center;
  }
  &__img {
    border-radius: 10px;
    overflow: hidden;
    img {
      transition: all .3s ease-in-out;
    }
  }
  &__content-left {
    margin-left: 10px;
  }
  &__title-desc {
    color: #010101;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 2px;
  }
  &__published {
    color: #9d9aa2;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
  &__btn {
    color: #9d9aa2;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
}
.genres-cus-title {
  color: #010101;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  margin-bottom: 12px;
}
.genres {
  ul {
    margin: -10px 0 0 -10px;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    @media (max-width: 767px) {
      width: 600px;
      overflow-x: auto;
    }
    li {
      margin: 10px 0 0 10px;
    }
    .genre-button {
      margin: 0;
      @media (max-width: 767px) {
        min-height: 32px;
        padding: 0 12px;
      }
    }
  }
}
.comics-add-box {
  @media (max-width: 1023px) {
    display: flex;
    justify-content: space-between;
    .comic-archive-item {
      max-width: 32%;
    }
  }
  div.comics .comic-thumbnail .views,
  article.comics .comic-thumbnail .views {
    font-size: 10px;
    line-height: 140%;
    font-weight: 400;
    padding: 0 0 4px 4px;
  }
  div.comics .comic-thumbnail .views > span,
  article.comics .comic-thumbnail .views > span {
    padding: 2px 4px;
  }
  div.comics .comic-thumbnail .episodes,
  article.comics .comic-thumbnail .episodes {
    font-size: 10px;
    font-weight: 500;
    border-radius: 10px 0px;
    background: rgba(1, 1, 1, 0.30);
    backdrop-filter: blur(14px);
  }
  div.comics .comic-thumbnail .label.adult,
  article.comics .comic-thumbnail .label.adult {
    font-size: 10px;
    padding: 3px 4.5px;
  }
}
