.genre {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    border: none;
    box-sizing: border-box;
    cursor: pointer;
    white-space: nowrap;
    background: $button-white;
    min-height: 36px;
    position: relative;
    span {
      position: relative;
      z-index: 1;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 8px;
      background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
      opacity: 0;
      transition: all .3s ease-in-out;
    }
    @media (min-width: 1250px) {
      &:hover {
        &::before {
          opacity: 1;
        }
      }
    }
    &s {
        &-grid {
            display: flex;
            justify-content: space-between;
            gap: 6px;
        }
    }
}
