.loadmore {
    padding: 10px 28px;
    border: 1px solid $black;
    font-size: 14px;
    border-radius: 30px;
    background: transparent;
    cursor: pointer;
    transition: all .3s ease-in-out;
    @media (min-width: 1250px) {
      &:hover {
          border-color: $yellow;
      }
    }


    &.d-none {
        display: none;
    }

    @media (min-width: 768px) {
        padding: 12px 32px;
    }

    &-wrapper {
        margin-bottom: 36px;

        @media (max-width: 767px) {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }

        @media (min-width: 768px) {
            margin-bottom: 60px;

            .container {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                width: 100%;
            }
        }
    }

    &-icon {
        width: 100%;
        height: 100%;
        max-width: 44px;
        max-height: 44px;
        object-fit: contain;
        object-position: center;

        &.d-none {
            display: none;
        }
    }
}
