.cta-banner {
    min-height: 300px;
    border-radius: 20px;
    font-size: 14px;
    line-height: 20px;
    &:not(.no-image) {
        .section-content {
            padding: 22px 16px 13px;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            display: flex;
            flex-direction: column;
            gap: 16px;
            &.center {
                justify-content: center;
            }
            &.bottom {
                justify-content: flex-end;
            }
            &.between {
                justify-content: space-between;
            }
        }
        img {
            border-radius: 20px;
            width: 100%;
        }
    }
}
