.page-template-page-genres {
  main {
    padding-top: 77px;
  }
}
.all-comics {
  margin-top: 10px;
}
.comics {
    &-sorting {
        &__wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 24px;
            @media all and (max-width: 767px) {
              margin-bottom: 10px;
            }
        }
    }

    &-grid {
        margin-bottom: 24px;
    }
}
.comics-sorting {
  position: relative;
  z-index: 2;
  &__opener {
    position: relative;
    color: #010101;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    display: flex;
    align-items: center;
    padding-right: 14px;
    cursor: pointer;
    svg {
      margin-right: 6px;
    }
    &::after {
      border-bottom: 1px solid #010101;
      border-right: 1px solid #010101;
      content: '';
      display: block;
      width: 5.5px;
      height: 5.5px;
      right: 0;
      margin-top: -4px;
      pointer-events: none;
      position: absolute;
      top: 50%;
      -webkit-transform-origin: 66% 66%;
      transform-origin: 66% 66%;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
      -webkit-transition: all .15s ease-in-out;
      transition: all .15s ease-in-out;
    }
    &.active {
      &::after {
        -webkit-transform: rotate(-135deg);
        transform: rotate(-135deg);
        margin-top: -3px;
      }
    }
  }
  &__drop {
    position: absolute;
    top: 100%;
    right: 0;
    border-radius: 10px;
    border: 1px solid #f6f6f6;
    background: #fff;
    box-shadow: 0px 1px 30px 0px rgba(61, 61, 61, 0.08);
    backdrop-filter: blur(27px);
    right: 0;
    margin: 0;
    padding: 0;
    list-style: none;
    width: 168px;
    padding: 10px;
    display: none;
    &.show {
      display: block;
    }
    li {
      & + li {
        margin-top: 6px;
      }
    }
    input {
      position: absolute;
      left: -9999px;
      &:checked {
        & + .comics-sorting__text {
          background: #010101;
          color: #fff;
        }
      }
    }
  }
  &__text {
    border-radius: 8px;
    background: #f3f4f6;
    display: block;
    padding: 6px 12px;
    color: #010101;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    cursor: pointer;
    transition: all .3s ease-in-out;
    @media (min-width: 1250px) {
      &:hover {
          background: $yellow;
      }
    }
  }
  &__sub-title {
    color: #4c4c4c;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    margin: 0;
  }
}
.all-comics .genres {
  margin-bottom: 22px;
}
.genre {
    &s {
        @media all and (max-width: 767px) {
            display: grid;
            grid-template-rows: repeat(2, max-content);
            grid-auto-flow: column;
            gap: 6px;
            overflow-x: auto;
            overflow-y: hidden;
            white-space: nowrap;
            box-sizing: border-box;
            height: max-content;
            padding-bottom: 10px;

            &::-webkit-scrollbar {
                height: 3px;

                &-thumb {
                    border-radius: 10px;
                    background: $black;
                }

                &-track {
                    background: $button-white;
                }
            }
        }

        &-filter {
            margin-bottom: 24px;
        }
    }

    &-button {
      position: relative;
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 8px;
        transition: all .3s ease-in-out;
        background: linear-gradient(275deg, #FFD233 0.95%, #FEEC49 47.84%, #FFD233 98.64%);
        opacity: 0;
      }
      span {
        position: relative;
        z-index: 1;
      }
      &.active {
        background: #feec49;
        &:hover {
          background: #feec49;
          &::after {
            opacity: 0;
          }
        }
      }
      @media all and (min-width: 1250px) {
          &:hover {
            background: #f3f4f6;
            &::after {
              opacity: 1;
            }
          }
      }
      @media all and (min-width: 768px) {
          margin-bottom: 6px;
      }
    }
}
