.page-title h1 {
  margin: 0;
}
.page-title {
  margin-bottom: 40px;
  @media (max-width: 767px) {
    margin-bottom: 24px;
  }
}
.contacts-info {
  .container {
    display: flex;
    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  &__item {
    & + .contacts-info__item {
      margin-top: 30px;
    }
  }
  &__title {
    color: #010101;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 12px;
  }
  &__email {
    display: flex;
    align-items: center;
    color: #010101;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    text-decoration: underline;
    svg {
      margin-right: 6px;
    }
  }
}
.social-list {
  margin-left: 104px;
  @media (max-width: 767px) {
    margin: 28px 0 0;
  }
  &__title {
    color: #010101;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: 2px;
  }
}
.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  li {
    & + li {
      margin-left: 8px;
    }
  }
  a {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
    background: #efefef;
    transition: all .3s ease-in-out;
    svg {
      fill: #010101;
      transition: all .3s ease-in-out;
    }
    @media (min-width: 1250px) {
      &:hover {
        svg {
          fill: #febc49;
        }
      }
    }
  }
}
